
import { defineComponent } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { ICategoryForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import CategoryForm from '@/views/catalog/category/forms/CategoryForm.vue'

export interface IData {
  model: ICategoryForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [saveForm, saveStatus, saveError] = useFetching((dataForm: ICategoryForm) => {
      return store.dispatch('category/createForm', dataForm)
    }) as ResponseFetching<Promise<ICategoryForm>>

    return {
      saveForm,
      saveStatus,
      saveError
    }
  },

  components: {
    PageCard,
    CategoryForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: {
        parentId: null,
        location: '',
        name: '',
        description: '',
        metaTitle: '',
        metaDescription: '',
        metaKeyword: '',
        image: '',
        seoUrl: '',
        isTop: false,
        isActive: true,
        sortOrder: 0,
        filters: []
      }
    }
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'category' })
    },

    async handleSubmit () {
      const data: ICategoryForm = await this.saveForm(this.model)
      if (typeof data !== 'undefined') {
        this.model = data
      }

      if (this.model?.id) {
        this.$router.push({ name: 'category-update', params: { id: this.model.id } })
      }
    }
  }
})
